// 
const fuc = () => {};

//  window  api
const AMap = window.AMap;

const errorFormat = (error)=>{
        switch (error.code) {
        case error.TIMEOUT:
            return ('');
        case error.POSITION_UNAVAILABLE:
            return ('');
        case error.PERMISSION_DENIED:
            return ('GPS');
        case error.UNKNOWN_ERROR:
            return ('');
        default:
            return (error);
    }
};
const getLocation = ({
    success = fuc,
    error = fuc
} = {}) => {

    if (AMap) {
        let MyMap = new AMap.Map('iCenter');
        MyMap.plugin('AMap.Geolocation', function() {
            let geolocation = new AMap.Geolocation({
                enableHighAccuracy: true, //:true
                timeout: 20000, //10
                GeoLocationFirst: true, // falsetruePCIP
                // maximumAge: 30000, //00
                convert: true, //true
            });
            MyMap.addControl(geolocation);
            geolocation.getCurrentPosition();
            AMap.event.addListener(geolocation, 'complete', success); //
            AMap.event.addListener(geolocation, 'error', (data)=>{
                error(errorFormat(data));
            }); //
        });
    }else{
        error("");
    }

};

export default getLocation;


